export const CAMPER_HEIGHT = () => {
  let HeightArr = [];
  for (let i = 79; i <= 201; i++) {
    let obj = {
      id: i,
      name: i + 'CM'
    };
    if (i === 79) {
      obj.name = '80CM';
    };
    if (i === 201) {
      obj.name = '200CM';
    };
    HeightArr.push(obj);
  };
  return HeightArr;
};

export const CAMPER_WEIGHT = () => {
  let WeightArr = [];
  for (let i = 9; i <= 100; i++) {
    let obj = {
      id: i,
      name: i + 'KG'
    };
    if (i === 9) {
      obj.name = '10KG';
    };
    if (i === 100) {
      obj.name = '99KG';
    };
    WeightArr.push(obj);
  };
  return WeightArr;
};

export const CAMPER_SHOOES = () => {
  let ShooesArr = [];
  for (let i = 155; i <= 480; i += 5) {
    let obj = {
      id: i,
      name: i / 10 + ''
    };
    ShooesArr.push(obj);
  };
  return ShooesArr;
};

